@charset "UTF-8";
html {
  height: 100%; }

body {
  font-family: 'Arial', sans-serif;
  height: 100%;
  margin: 0;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background: linear-gradient(#9CC4E4, #0A0E46); }

/* Header */
.header {
  padding: 20px;
  display: flex;
  justify-content: flex-start;
  align-items: center; }

.header-container {
  display: flex;
  justify-content: center;
  /* Centra horizontalment */
  align-items: center;
  /* Centra verticalment */
  width: 100%;
  height: 100px; }

.logo {
  font-size: 1.5rem;
  font-weight: bold;
  color: #202f66;
  margin-right: 15em; }

.logo-container {
  border-radius: 90px;
  border: 1px solid transparent;
  background-color: #66D9C2;
  color: #202f66;
  padding: 1em 20em 1em 1em;
  margin: 1em 0; }

.highlight {
  font-size: 3.5rem;
  color: #66D9C2;
  font-family: 'Arial', sans-serif;
  font-weight: bold; }

.cta-button {
  display: block;
  margin: auto;
  margin-top: 1em;
  background-color: #66D9C2;
  color: #0A0E46;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
  font-size: 1rem;
  font-weight: bold; }

.cta-button:hover {
  background-color: #ffffff;
  color: #66D9C2; }

.main-text-1 {
  /* Creiem en una era digital, on el consum tradicional i l’online es fusionen */
  margin-top: 2em;
  font-family: 'Georgia';
  font-style: normal;
  font-weight: 400;
  font-size: 50px;
  line-height: 107%;
  text-align: center;
  /* Color 3 */
  color: #FFFFFF;
  /* Inside auto layout */
  flex: none;
  order: 0;
  align-self: stretch;
  flex-grow: 0; }

.main-text-2 {
  /* Connectant les companyies amb els seus clients com mai abans ho havien fet. */
  margin-top: 1em;
  max-width: 20em;
  /* H2_Subtítol */
  font-family: 'Arial';
  font-style: normal;
  font-weight: 500;
  font-size: 22px;
  line-height: 29px;
  text-align: center;
  /* Color 3 */
  color: #FFFFFF;
  /* Inside auto layout */
  flex: none;
  order: 1;
  flex-grow: 0; }

/* Footer */
.footer {
  background-color: #ffffff;
  padding: 2em 10em 2em 10em;
  color: #202f66;
  margin-top: 2em;
  max-width: none;
  font-size: 0.75em; }

.footer-bottom {
  color: #ffffff;
  margin-top: 2em;
  margin-bottom: 2em; }

.footer-logo {
  font-size: 1.5rem;
  font-weight: bold; }

.footer-links h4 {
  margin-bottom: 10px; }

.footer-links p,
.footer-links ul {
  font-size: 0.9rem; }

.footer-links ul {
  list-style: none; }

.footer-links a {
  color: #0A0E46;
  text-decoration: underline; }

.footer-links a:hover {
  text-decoration: underline; }

.social-icons a {
  color: #0A0E46;
  padding: 0.25em; }

.footer-bottom {
  text-align: center;
  margin-top: 20px;
  font-size: 0.8rem; }
